import React, { useState, useEffect } from "react"
import { Link } from "gatsby"
import { AnimatePresence, motion, useViewportScroll, useTransform } from "framer-motion";
import {
  BrowserRouter,
  Switch,
  Route
} from "react-router-dom";
import Layout from "../components/layout"
import SEO from "../components/seo"
import previcon from "../images/prev.png"
import nexticon from "../images/next.png"
import juneau from "../images/juneau.jpg"
import columbus from "../images/columbus.jpg"
import noora from "../images/noora.jpg"
import alaska from "../images/alaska.jpg"

import juneaupage from "../pages/juneau"
import columbuspage from "../pages/columbus"
import noorapage from "../pages/noora"
import alaskapage from "../pages/alaska"


const slides = [
  {
    title: "Columbus",
    image: columbus, 
    link: "columbus"
  },
  {
    title: "Alaska",
    image: alaska,
    link: "alaska"
  },
  {
    title: "Juneau",
    image: juneau,
    link: "juneau"
  },
  {
    title: "Noora",
    image: noora,
    link: "noora"
  }
];

function useTilt(active) {
  const ref = React.useRef(null);

  React.useEffect(() => {
    if (!ref.current || !active) { return; }

    const state = {
      rect: undefined,
      mouseX: undefined,
      mouseY: undefined
    };

    let el = ref.current;

    const handleMouseMove = (e) => {
      if (!el) {
        return;
      }
      if (!state.rect) {
        state.rect = el.getBoundingClientRect();
      }
      state.mouseX = e.clientX;
      state.mouseY = e.clientY;
      const px = (state.mouseX - state.rect.left) / state.rect.width;
      const py = (state.mouseY - state.rect.top) / state.rect.height;

      el.style.setProperty("--px", px);
      el.style.setProperty("--py", py);
    };

    el.addEventListener("mousemove", handleMouseMove);

    return () => {
      el.removeEventListener("mousemove", handleMouseMove);
    };
  }, [ref, active]);

  return ref;
}

const initialState = {
  slideIndex: 0
}

const slidesReducer = (state, event) => {
  if (event.type === "PREVIOUS") {
    return {
      ...state,
      slideIndex: (state.slideIndex + 1) % slides.length
    };
  }
  if (event.type === "NEXT") {
    return {
      ...state,
      slideIndex:
        state.slideIndex === 0 ? slides.length - 1 : state.slideIndex - 1
    };
  }
};

function Slide({slide, offset}) {
  const active = offset === 0 ? true : null;
  const ref = useTilt(active);
  
  return (
    <div
      ref={ref}
      className="slide"
      data-active={active}
      style={{
        "--offset": offset,
        "--dir": offset === 0 ? 0 : offset > 0 ? 1 : -1
      }}
    >
      <div
        className="slideBackground"
        style={{
          backgroundImage: `url('${slide.image}')`
        }}
      />
      <div className="slideContent">
        <div className="slideContentInner">
         <Link to={slide.link}> <h1 className="slide-title">{slide.title}</h1></Link>
        </div>
      </div>
    </div>);
}

function IndexPage() {
  const [state, dispatch] = React.useReducer(slidesReducer, initialState); 
  return (
     <BrowserRouter>
        <Route render={({ location }) => ( 
        <AnimatePresence initial={true} exitBeforeEnter>
        <motion.div 
          initial={{ opacity: 0}}
          animate={{ opacity: 1}}
          transition={{ duration: 0.8 }}
        >
        <Switch location={location} key={location.pathname}>
        <Route path="/juneau" component={juneaupage} />
        <Route path="/alaska" component={alaska} />
        <Route path="/columbus" component={columbuspage} />
        <Route path="/noora" component={noorapage} />

   <Layout>
   <div className="work-wrapper">
 
    <SEO title="Web Designer and Developer based in London" />
    <div className="slides">

     <button className="slideButton prev" onClick={() => dispatch({ type: "PREVIOUS" })}><img src={previcon} /></button>
      
      {[...slides, ...slides, ...slides].map((slide, i) => {
        let offset = slides.length + (state.slideIndex - i);
        return <Slide slide={slide} offset={offset} key={i} />;
      })}

      <button className="slideButton next" onClick={() => dispatch({ type: "NEXT" })}><img src={nexticon} /></button>
  </div>
 </div>
</Layout>
 </Switch>
            </motion.div>
            </AnimatePresence>
            )}
        
        />
        </BrowserRouter>
)}

export default IndexPage
